import React, { useState } from 'react';
import { Section } from './ui/Section';
import { motion, AnimatePresence } from 'framer-motion';
import { X } from 'lucide-react';

export const Gallery = () => {
  const images = [
    { src: '/assets/site-5.jpg', alt: 'Project 1', span: 'col-span-1 md:col-span-2 row-span-2' },
    { src: '/assets/site-6.jpg', alt: 'Project 2', span: 'col-span-1 row-span-1' },
    { src: '/assets/site-7.jpg', alt: 'Project 3', span: 'col-span-1 row-span-1' },
    { src: '/assets/site-8.jpg', alt: 'Project 4', span: 'col-span-1 md:col-span-2 row-span-1' },
    { src: '/assets/site-9.jpg', alt: 'Project 5', span: 'col-span-1 row-span-1' },
    { src: '/assets/site-10.jpg', alt: 'Project 6', span: 'col-span-1 row-span-1' },
  ];

  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  return (
    <Section id="gallery" background="white">
      <div className="text-center mb-16">
        <h3 className="text-corporate-accent font-bold tracking-wider uppercase mb-2">Projects</h3>
        <h2 className="text-3xl md:text-4xl font-bold text-corporate-black"></h2>
        <p className="mt-4 text-gray-600 max-w-2xl mx-auto">
          
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 auto-rows-[200px]">
        {images.map((img, index) => (
          <motion.div
            key={index}
            className={`relative overflow-hidden rounded-lg cursor-pointer group ${img.span}`}
            whileHover={{ scale: 1.02 }}
            transition={{ duration: 0.3 }}
            onClick={() => setSelectedImage(img.src)}
          >
            <img
              src={img.src}
              alt={img.alt}
              className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-110"
            />
            <div className="absolute inset-0 bg-black/0 group-hover:bg-black/30 transition-colors duration-300 flex items-center justify-center opacity-0 group-hover:opacity-100">
              <span className="text-white font-medium tracking-wider border border-white px-4 py-2">VIEW</span>
            </div>
          </motion.div>
        ))}
      </div>

      {/* Lightbox */}
      <AnimatePresence>
        {selectedImage && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-50 bg-black/90 flex items-center justify-center p-4"
            onClick={() => setSelectedImage(null)}
          >
            <button
              className="absolute top-4 right-4 text-white p-2 hover:bg-white/10 rounded-full"
              onClick={() => setSelectedImage(null)}
            >
              <X className="w-8 h-8" />
            </button>
            <motion.img
              initial={{ scale: 0.9 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0.9 }}
              src={selectedImage}
              alt="Full size"
              className="max-w-full max-h-[90vh] object-contain rounded-sm"
              onClick={(e) => e.stopPropagation()}
            />
          </motion.div>
        )}
      </AnimatePresence>
    </Section>
  );
};
