import React from 'react';
import { Section } from './ui/Section';
import { Building2, HardHat, Truck, Ruler } from 'lucide-react';
import { motion } from 'framer-motion';

const ServiceCard = ({ icon: Icon, title, description, delay }: { icon: any, title: string, description: string, delay: number }) => (
  <motion.div
    initial={{ opacity: 0, y: 20 }}
    whileInView={{ opacity: 1, y: 0 }}
    viewport={{ once: true }}
    transition={{ duration: 0.5, delay }}
    className="bg-white p-8 rounded-lg shadow-lg hover:shadow-xl transition-shadow border border-gray-100 group"
  >
    <div className="w-14 h-14 bg-corporate-light rounded-lg flex items-center justify-center mb-6 group-hover:bg-corporate-accent transition-colors duration-300">
      <Icon className="w-8 h-8 text-corporate-dark group-hover:text-white transition-colors duration-300" />
    </div>
    <h3 className="text-xl font-bold text-corporate-black mb-3">{title}</h3>
    <p className="text-gray-600 leading-relaxed">{description}</p>
  </motion.div>
);

export const Services = () => {
  const services = [
    {
      icon: Building2,
      title: "",
      description: ""
    },
    {
      icon: Truck,
      title: "",
      description: ""
    },
    {
      icon: HardHat,
      title: "",
      description: ""
    },
    {
      icon: Ruler,
      title: "",
      description: ""
    }
  ];

  return (
    <Section id="services" background="light">
      <div className="text-center mb-16">
        <h3 className="text-corporate-accent font-bold tracking-wider uppercase mb-2">Our Services</h3>
        <h2 className="text-3xl md:text-4xl font-bold text-corporate-black"></h2>
        <p className="mt-4 text-gray-600 max-w-2xl mx-auto">
          
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
        {services.map((service, index) => (
          <ServiceCard
            key={index}
            icon={service.icon}
            title={service.title}
            description={service.description}
            delay={index * 0.1}
          />
        ))}
      </div>
    </Section>
  );
};
